<template>
  <div>
    <!-- <b-icon v-if="!show" icon="square" @click="show=!show"></b-icon>
    <b-icon v-if="show" icon="check-square" @click="show=!show"></b-icon> -->
<!--     <pre>{{order}}</pre>-->
    <div class="mx-auto shadow-lg  text-center col-sm-8" v-if="showOrder">
      <b-card no-body bg-variant="primary" class="my-5" text-variant="white">
        <template v-slot:header>
          <p class="text-center">
            <b-spinner type="grow" small class="mr-3"></b-spinner>Success
            <b-spinner type="grow" small class="ml-3"></b-spinner>
          </p>
        </template>
        <b-card-body>
          <p class="text-center">Order Submitted Successfully!!</p>
          <p class="text-center">Order ID no: {{ order_id }}</p>
        </b-card-body>
      </b-card>
    </div>
    <b-toast id="my-toast" :variant="isPossible ? 'info':'warning'">
        <template v-slot:toast-title>
          <div class="ml-auto text-center">
            <b-img blank :blank-color="isPossible ? '#008000':'#ff5555'" class="" width="12" height="12"></b-img>
            <strong class="ml-1" v-if="isPossible">Success!!</strong>
            <strong class="ml-1" v-else>Error!!</strong>
          </div>
        </template>
        <p class="text-center">
          <b-spinner type="grow" small class="mr-3"></b-spinner>{{errorMessage}}
          <b-spinner type="grow" small class="ml-3"></b-spinner>
        </p>
    </b-toast>
    <b-container class="bg-white" fluid v-if="!showOrder">
      <b-row>
        <b-col md="8">
          <b-container class="bg-light shadow-sm my-2">
            <b-form class="mt-2"
              v-if="!canOrder">
              <b-row class="">
                <b-col>
                  <b-form-group
                    id="input-group-1"
                    label="Full Name:"
                    label-for="input-1"
                  >
                    <b-form-input
                      id="input-1"
                      v-model="form.name"
                      type="text"
                      required
                      placeholder="Enter your first and last name here"
                    ></b-form-input>
                  </b-form-group>
                  <b-form-group
                    id="input-group-2"
                    label="Phone Number:"
                    label-for="input-2"
                  >
                    <b-form-input
                      id="input-2"
                      v-model="form.phone"
                      type="tel"
                      required
                      placeholder="Enter your phone number here"
                    ></b-form-input>
                  </b-form-group>
                </b-col>
                <b-col>
                  <b-form-group
                    id="input-group-3"
                    label="Region:"
                    label-for="input-3"
                  >
                    <b-form-select
                      id="input-3"
                      v-model="form.region"
                      :options="regions"
                      required
                      placeholder="Enter your region"
                    ></b-form-select>
                  </b-form-group>
                  <b-form-group
                    id="input-group-4"
                    label="City:"
                    label-for="input-4"
                  >
                    <b-form-select
                      id="input-4"
                      v-model="form.city"
                      :options="regions"
                      required
                      placeholder="Enter your city"
                    ></b-form-select>
                  </b-form-group>
                  <b-form-group
                    id="input-group-5"
                    label="Area:"
                    label-for="input-5"
                  >
                    <b-form-select
                      id="input-5"
                      v-model="form.area"
                      :options="regions"
                      required
                      placeholder="Enter your area"
                    ></b-form-select>
                  </b-form-group>
                  <b-form-group
                    id="input-group-6"
                    label="Address:"
                    label-for="input-6"
                  >
                    <b-form-input
                      id="input-6"
                      v-model="form.address"
                      type="text"
                      required
                      placeholder="For example:House# 12,Street# 13,Road# 14"
                    ></b-form-input>
                  </b-form-group>
                  <div class="d-flex justify-content-end">
                    <b-button type="reset" squared  @click="resetOnAction" variant="danger">Reset</b-button>
                    <b-button type="submit" class="ml-2" @click="submitOnAction" squared variant="primary" right>Submit</b-button>
                  </div>
                </b-col>
              </b-row>
            </b-form>
          </b-container>
          <b-container class="mb-2" v-for="(product, index) in order.products" :key="index" :value="index">
           <b-container class="shadow-lg" fluid>
            <b-media no-body>
              <b-media-aside vertical-align="center">
                <b-img class="d-flex img-thumbnill align-self-center" width="150" height="150" :src="product.image.link"></b-img>
              </b-media-aside>
              <b-media-body>
                <b-row>
                <b-col md="6" class="text-center" align-self="center">
                  <span class="">
                    <strong>{{product.name}}</strong><br>
                    <small v-for="(key,value,i) in product.specifications" :key="i" style="color:grey">
                      {{key}}:{{value}},
                    </small>
                  </span>
                </b-col>
                <b-col md="2" class="text-center" align-self="center">
                  <span id="prev-price">
                    <strike>&#2547;{{previousPrice(product.curPrice,product.discount)}}</strike>
                    <strong id="discount" class="ml-1 d-block">{{ -product.discount }}%</strong>
                  </span>
                  <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
                  <br>
                </b-col>
                <b-col md="4" align-self="center" class="text-center">
                  <p>{{product.quantity}}x</p>
                </b-col>
                </b-row>
              </b-media-body>
            </b-media>
           </b-container>
          </b-container>
        </b-col>
        <b-col class="mt-2">
          <b-card no-body class="mt-2">
            <template v-slot:header>
              <h4 class="mb-0">Order Summary</h4>
            </template>
            <b-card-body>
              <dl class="row">
                <dt class="col-6">Subtotal({{itemCount}}/{{productCount}} items)</dt>
                <dd class="col-6">&#2547;{{ order.subTotal }}</dd>
                <dt class="col-6">Delivery charge</dt>
                <dd class="col-6">&#2547;{{order.deliveryCharge}}</dd>
                <dt class="col-6">Total</dt>
                <dd class="col-6">&#2547;{{order.deliveryCharge+order.subTotal}}</dd>
              </dl>
            </b-card-body>
            <template v-slot:footer>
              <b-container fluid class="d-flex justify-content-center">
                <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
                  <div class="text-center shadow bg-success">
                    <b-button variant="success" id="confirmButton"
                      :disabled="!canOrder"
                      @click="sendOrder"
                    >
                      Confirm Order
                    </b-button>
                  </div>
                </b-overlay>
              </b-container>
            </template>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import api from '../../../plugins/axios'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'OrderSubmitDetails',
  data () {
    return {
      isPossible: '',
      errorMessage: '',
      busy: false,
      order_id: '',
      showOrder: false,
      order: this.$route.query.order,
      images: [
        'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg',
        'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      ],
      canOrder: false,
      form: {
        name: '',
        phone: '',
        region: '',
        city: '',
        area: '',
        address: ''
      },
      regions: [
        { value: 'Dhaka', text: 'Dhaka' },
        { value: 'Chattogram', text: 'chittagong' }
      ]
    }
  },
  computed: {
    ...mapGetters([
      'getConfig',
      'user',
      'selectedProducts'
    ]),
    productCount () {
      return this.order.products ? this.order.products.length : ''
    },
    itemCount () {
      if (!this.order.products) {
        return ''
      }
      return this.order.products.reduce((count, product) => count + product.quantity, 0)
    }
  },
  methods: {
    ...mapActions([
      'cartRemove'
    ]),
    onHidden () {
      this.$refs.button.focus()
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    submitOnAction () {
      this.canOrder = true
    },
    sendOrder () {
      this.busy = true
      console.log('order sent')
      api.post('/order/add', {
        products: this.order.products,
        orderPhoneNo: this.form.phone,
        email: 'dummy@text.fao',
        address: this.form.address,
        phoneNo: this.user.phoneNo
      }, this.getConfig())
        .then(response => {
          this.busy = false
          // this.isPossible = true
          // this.errorMessage = 'order submitted successfully, order id:'
          // this.$bvToast.show('my-toast')
          this.dismissableAlert = true
          console.log(`order submitted successfully, order id: ${response.data.orderID}`)
          console.log(response)
          // this.$router.push({ name: 'Home' })
          this.order_id = response.data.orderID
          this.showOrder = true
          if (this.selectedProducts.length) {
            this.cartRemove(this.selectedProducts)
          }
          // this.isPossible = true
          // this.errorMessage = 'order submitted successfully, order id:'
          // this.$bvToast.show('my-toast')
        })
        .catch(error => {
          this.busy = false
          this.isPossible = false
          this.errorMessage = 'order submission failed'
          this.$bvToast.show('my-toast')
          console.log('order submission failed')
          console.log(error.response)
        })
    },
    resetOnAction () {
      this.form = {
        name: this.user.name,
        phone: this.user.phoneNo,
        region: '',
        city: '',
        area: '',
        address: this.user.address
      }
    }
  },
  created () {
    if (!this.order || !this.order.products) {
      this.$router.push({
        name: 'Home'
      })
    } else {
      this.resetOnAction()
    }
  }
}
</script>

<style scoped>
  #confirmButton {
    width: 100%
  }
</style>
