<template>
  <b-container class="bg-light" fluid>
    <b-row class="my-2" v-if="cartProducts.length">
      <b-col align-self="center">
        <span style="cursor: pointer;">
          <b-icon v-if="allSelected" scale="1.1" icon="check-square" @click="handleAllDeselect"></b-icon>
          <b-icon v-else icon="square" @click="handleAllSelect"></b-icon>
        </span>
        {{ allSelected ? 'Deselect' : 'Select' }} All
      </b-col>
      <b-col align-self="center" v-if="cartProducts.length">
        <b-button @click="handleDeleteSelected" variant="danger" :disabled="this.selectedProducts.length === 0">
          Delete Selected
        </b-button>
      </b-col>
    </b-row>
    <b-row>
      <b-col md="8" class="my-2">
        <b-container fluid>
          <b-container class="mb-2" v-model="selectedProducts" v-for="(product, index) in cartProducts" :key="index" :value="index">
            <!-- <pre>{{product}}</pre> -->
            <b-container class="shadow-sm" fluid>
                <b-row>
                  <b-col cols="1" align-self="center">
                    <div style="cursor: pointer;">
                      <b-icon v-if="isSelected(index)" scale="1.1" icon="check-square" @click="handleCartDeselect(index)"></b-icon>
                      <b-icon v-else icon="square" @click="handleCartSelect(index)"></b-icon>
                    </div>
                  </b-col>
                  <b-col cols="11" class="">
                    <b-media no-body>
                      <b-media-aside vertical-align="center">
                        <router-link :to="{ name: 'CertainProduct', params: { id: product.id } }">
                          <b-img class="d-flex img-thumbnill align-self-center" width="150" height="150" :src="product.image.link"></b-img>
                        </router-link>
                      </b-media-aside>
                      <b-media-body>
                        <b-row>
                          <b-col md="6" class="text-center" align-self="center">
                            <span class="">
                              <router-link
                                class="text-decoration-none"
                                style="color: black"
                                :to="{ name: 'CertainProduct', params: { id: product.id } }"
                              >
                                <strong>{{product.name}}</strong><br>
                              </router-link>
                              <small v-for="(key,value,index) in product.specifications" :key="index" style="color:grey">
                                {{value}}:{{key}},
                              </small>
                            </span>
                          </b-col>
                          <b-col md="2" class="text-center">
                            <span id="prev-price">
                              <strike>&#2547;{{previousPrice(product.curPrice,product.discount)}}</strike>
                              <strong id="discount" class="ml-1 d-block">{{ -product.discount }}%</strong>
                            </span>
                            <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
                            <br>
                            <b-avatar button icon="trash" variant="danger" size=""
                              @click="handleCartRemove(index)"></b-avatar>
                          </b-col>
                          <b-col md="4" align-self="center" class="text-center">
                            <span class="d-inline">
                              <b-button class="text-center" variant="light" @click="product.quantity++">+</b-button>
                              <span>{{product.quantity}}</span>
                              <b-button class="text-center" variant="light"
                              @click="product.quantity>1?product.quantity--:product.quantity">-</b-button>
                            </span>
                          </b-col>
                        </b-row>
                      </b-media-body>
                    </b-media>
                  </b-col>
                </b-row>
            </b-container>
          </b-container>
        </b-container>
    </b-col>
    <b-col md="4">
      <b-card no body class="mt-2">
        <template v-slot:header>
          <h4 class="mb-0">Order Summary</h4>
        </template>
        <b-card-body>
          <dl class="row">
            <dt class="col-6">Subtotal({{itemCount}}/{{selectedProducts.length}} items)</dt>
            <dd class="col-6">&#2547;{{ order.subTotal }}</dd>
            <dt class="col-6">Delivery charge</dt>
            <dd class="col-6">&#2547;{{order.deliveryCharge}}</dd>
            <dt class="col-6">Total</dt>
            <dd class="col-6">&#2547;{{total}}</dd>
          </dl>
        </b-card-body>
        <template v-slot:footer>
          <div class="text-center bg-success">
            <b-overlay :show="busy1" opacity="0.4" spinner-small
                      spinner-variant="primary" class="d-inline block" @hidden="onHidden">
              <b-button variant="success" id="proceedButton" @click="handleProceeding"
                :disabled="!selectedProducts.length || busy1"
              >
                Proceed to Pay
              </b-button>
            </b-overlay>
          </div>
        </template>
      </b-card>
    </b-col>
    </b-row>
    <!-- <pre>{{selectedProducts}}</pre>
    <pre>{{order}}</pre> -->
  </b-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'CartDetails',
  data () {
    return {
      // from here
      busy1: false
    }
  },
  computed: {
    ...mapGetters([
      'authToken',
      'cartProducts',
      'selectedProducts',
      'allSelected',
      'itemCount',
      'isSelected'
    ]),
    order () {
      const order = {
        products: [],
        deliveryCharge: 60,
        subTotal: 0
      }
      for (const idx of this.selectedProducts) {
        if (idx < this.cartProducts.length) {
          const product = this.cartProducts[idx]
          order.products.push(product)
          order.subTotal += product.curPrice * (1 + product.vat / 100) * product.quantity
        }
      }
      order.subTotal = Math.round(order.subTotal)
      return order
    },
    total () {
      return this.order.subTotal ? this.order.deliveryCharge + this.order.subTotal : 0
    }
  },
  methods: {
    ...mapActions([
      'cartRemove',
      'loadCart',
      'handleCartSelect',
      'handleCartDeselect',
      'handleAllSelect',
      'handleAllDeselect',
      'handleCartRemove',
      'handleDeleteSelected'
    ]),
    onHidden () {
      this.$refs.button.focus()
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    handleProceeding () {
      if (!this.authToken) {
        this.$bvModal.show('login-modal')
      } else {
        this.busy1 = true
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.order,
            removed: {
              indices: this.selectedProducts
            }
          }
        })
      }
    }
  },
  created () {
    this.loadCart(true)
  }
}
</script>

<style>
    #imageProduct {
        width: 100%;
        max-width:50px;
        max-height:50px;
        height: auto;
        border: 1px solid black;
    }
     #prev-price {
        color: grey;
    }
    #discount {
        color: red;
    }
    #cur-price {
        font-size: 120%;
        color: darkorange;
    }
    #proceedButton {
      width: 100%
    }
</style>
