<template>
<b-container fluid>
      <b-toast id="my-toast" :variant="isPossible ? 'info':'warning'">
        <template v-slot:toast-title>
          <div class="ml-auto text-center">
            <b-img blank :blank-color="isPossible ? '#008000':'#ff5555'" class="" width="12" height="12"></b-img>
            <strong class="ml-1" v-if="isPossible">Success!!</strong>
            <strong class="ml-1" v-else>Error!!</strong>
          </div>
        </template>
        <p class="text-center">
          <b-spinner type="grow" small class="mr-3"></b-spinner>{{errorMessage}}
          <b-spinner type="grow" small class="ml-3"></b-spinner>
        </p>
      </b-toast>
      <h1 class="ml-3">Order History</h1>
      <b-table :items="orders" :fields="fields" striped responsive="sm" class="mx-3">
        <template v-slot:cell(show_details)="row">
          <b-button size="sm" @click="row.toggleDetails" class="mr-2"
            :variant="row.detailsShowing ? 'danger' : 'success'" >
            {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
          </b-button>
        </template>
        <template v-slot:cell(date)="row">
          {{ convertToDate(row.item.currentStatus.date._seconds) }}
        </template>
        <template v-slot:cell(status)="row">
          {{ row.item.currentStatus.name }}
        </template>
        <template v-slot:cell(total)="row">
          {{ row.item.total }}
        </template>
        <template v-slot:cell(refund)="row">
          <!-- <b-container fluid class="d-flex justify-content-center"> -->
            <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
              <b-button size="sm" class="mr-2" v-if="row.item.currentStatus.name!=='ordered'" :disabled="busy" variant="warning"  @click="requestRefund(row.item.orderID)">
                refund
              </b-button>
            </b-overlay>
            <b-button size="sm" v-if="row.item.currentStatus.name==='ordered'" variant="danger" @click="cancelOrder(row.item.orderID)">
            cancel order
          </b-button>
          <!-- </b-container> -->
        </template>

        <template v-slot:row-details="row">
          <b-container class="mb-2 shadow" v-for="(product, index) in row.item.products" fluid :key="index" :value="index">
            <!-- <b-container class="shadow" fluid> -->
              <b-media no-body>
                <router-link :to="{ name: 'CertainProduct', params: { id: product.id } }">
                  <b-media-aside vertical-align="center">
                    <b-img class="d-flex img-thumbnill align-self-center" width="150" :src="product.image.link" :alt="product.image.name"></b-img>
                  </b-media-aside>
                </router-link>
                <b-media-body class="my-auto text-center">
                  <b-row>
                  <b-col md="6" class="my-auto text-center" align-self="center">
                    <span class="">
                      <router-link
                        class="text-decoration-none"
                        style="color: black"
                        :to="{ name: 'CertainProduct', params: { id: product.id } }"
                      >
                        <strong>{{product.name}}</strong><br>
                      </router-link>
                      <small v-for="(i,s) in product.specifications" :key="i" style="color:grey">
                        {{s}}:{{i}},
                      </small>
                    </span>
                  </b-col>
                  <b-col md="2" class="my-auto text-center">
                    <span id="prev-price">
                      <strike>&#2547;{{previousPrice(product.curPrice,product.discount)}}</strike>
                      <strong id="discount" class="ml-1 d-block">{{ -product.discount }}%</strong>
                    </span>
                    <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
                    <br>
                  </b-col>
                  <b-col md="4" align-self="center" class="text-center">
                    <p>{{product.quantity}}x</p>
                    <b-form-rating inline no-border v-if="product.hasOwnProperty('rating')"
                      readonly v-model="product.rating"  variant="warning"></b-form-rating>
                    <b-button v-b-modal="'ratingModal'+index+row.item.orderID"  variant="success"
                     v-if="!product.hasOwnProperty('rating')
                     && row.item.currentStatus.name!=='ordered' && row.item.currentStatus.name!=='confirmed'">Rate it</b-button>
                    <b-modal :id="'ratingModal'+index+row.item.orderID" ref="modal" title=""
                     @ok="rateProduct(row.item.orderID,index,product.rating)">
                      <label for="rate">Submit Your Rating</label>
                      <b-form-rating id="rate" v-model="product.rating"></b-form-rating>
                    </b-modal>
                  </b-col>
                  </b-row>
                </b-media-body>
              </b-media>
            <!-- </b-container> -->
          </b-container>
        </template>
      </b-table>
  <b-container class="d-flex justify-content-center">
    <b-overlay :show="busy1" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
      <b-button :disabled="busy1" variant="success" v-if="!orderLoadingDone" @click="loadOrders">
        Load More
      </b-button>
    </b-overlay>
  </b-container>
</b-container>
</template>

<script>
import { mapGetters } from 'vuex'
import api from '../../../plugins/axios'

export default {
  name: 'OrderShower',
  data () {
    return {
      fields: ['orderID', 'Date', 'Total', 'status', 'show_details', 'refund'],
      orders: [],
      orderLoadingDone: true,
      image: {
        link: 'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      },
      errorMessage: '',
      isPossible: '',
      busy: false,
      busy1: false
    }
  },
  computed: {
    ...mapGetters([
      'getConfig',
      'user',
      'userLoaded'
    ])
  },
  methods: {
    loadOrders () {
      if (!this.orderLoadingDone) this.busy1 = true
      console.log('orders requested for ' + this.user.phoneNo)
      const offset = this.orders.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      api.post('/orders/all', {
        phoneNo: this.user.phoneNo
      }, this.getConfig({ offset, limit }))
        .then(response => {
          console.log('orders loaded')
          console.log(response)
          this.orders = this.orders.concat(response.data.orders)
          this.busy1 = false
          if (response.data.orders.length < limit) {
            this.orderLoadingDone = true
          } else {
            this.orderLoadingDone = false
          }
        })
        .catch(error => {
          console.log('orders loading failed')
          console.log(error.response)
        })
    },
    rateProduct (orderid, index, rating) {
      // console.log(orderid,)
      api.post('/order/rate', {
        phoneNo: this.user.phoneNo,
        orderID: Number(orderid),
        orderIndex: Number(index),
        rating
      }, this.getConfig())
        .then(response => {
          console.log(`product-${this.order.products[index].id} rated ${rating}-stars`)
          console.log(response)
        })
        .catch(error => {
          console.log(`rating product-${this.order.products[index].id} failed`)
          console.log(error.response)
        })
    },
    requestRefund (orderID) {
      // this.busy = true
      api.post('/order/refund', {
        phoneNo: this.user.phoneNo,
        orderID
      }, this.getConfig())
        .then(response => {
          this.busy = false
          this.isPossible = true
          this.message = 'refund requested for order ' + this.orderID
          console.log(`refund requested for order-${orderID}`)
          console.log(response)
        })
        .catch(error => {
          this.busy = false
          this.isPossible = false
          this.errorMessage = error.response.data.error
          this.$bvToast.show('my-toast')
          console.log(`requesting refund for order-${orderID} failed`)
          console.log(error.response)
        })
    },
    cancelOrder (orderID) {
      api.delete('/order/cancel', this.getConfig(null, {
        phoneNo: this.user.phoneNo,
        orderID
      }))
        .then(response => {
          console.log(`canceled order-${orderID}`)
          console.log(response)
          this.orders = this.orders.filter(order => order.orderID !== orderID)
        })
        .catch(error => {
          console.log(`cancelling order-${orderID} failed`)
          console.log(error.response)
        })
    },
    previousPrice (curPrice, discount) {
      return Math.round(curPrice / (1 - discount / 100))
    },
    convertToDate (timestamp) {
      const unixTimestamp = timestamp
      const milliseconds = unixTimestamp * 1000
      const dateObject = new Date(milliseconds)
      const dateFormat = dateObject.toLocaleString()
      return dateFormat
    },
    getDiff (timestamp, curStatus) {
      const presentDate = new Date()
      const previousDate = this.convertToDate(timestamp)
      const difference = presentDate.getTime() - previousDate
      return (curStatus === 'delivered' && difference < 7)
    }
  },
  created () {
    if (this.userLoaded) {
      this.loadOrders()
    } else {
      const inCreated = setInterval(() => {
        if (this.userLoaded) {
          this.loadOrders()
          clearInterval(inCreated)
        }
      }, process.env.VUE_APP_RETRY_INTERVAL)
    }
  }
}
</script>

